@import url(https://fonts.googleapis.com/css?family=Noto+Sans&display=swap);
.text-center {
  text-align: center; }

.text-left {
  text-align: left; }

.text-right {
  text-align: right; }

.text-justify {
  text-align: justify; }

.title {
  font-size: 1.05rem;
  font-weight: 400; }
  .title--bolder {
    font-weight: bolder; }

.container {
  max-width: 1850px;
  margin: 0 auto;
  padding: 1rem; }

.space-between {
  display: flex;
  justify-content: space-between;
  align-items: center; }

.abroad {
  box-shadow: 0px -2px 7px -2px rgba(0, 0, 0, 0.75);
  border-radius: 2px;
  overflow: hidden;
  background: #fff; }

.wrap-head {
  padding: 0.7rem;
  border-bottom: 1px solid #ccc; }

.wrap-content {
  padding: 1.3rem; }

html {
  font-size: 100%; }

html,
body {
  height: 100%; }

*,
*::before,
*::after {
  box-sizing: border-box;
  margin: 0;
  padding: 0; }

body {
  font-family: 'Noto Sans', sans-serif;
  font-size: 0.8rem;
  line-height: 1.5;
  font-weight: 300;
  color: #6a717b;
  background-color: #f3f3f4; }

a {
  color: inherit;
  text-decoration: none; }

ul {
  list-style: none; }

img {
  max-width: 100%;
  display: block;
  height: auto; }

.activity {
  background: #fff; }
  .activity__header {
    background: #fff; }
  .activity__pointer {
    background: #1d83c6;
    color: #fff;
    display: block;
    padding: 0.2rem 0.5rem;
    border-radius: 2px;
    letter-spacing: 1px; }
    .activity__pointer--blue {
      background: #1d83c6; }
    .activity__pointer--ocean {
      background: #23c6c7; }
    .activity__pointer--green {
      background: #19b393; }
    .activity__pointer--red {
      background: #ed5564; }
  .activity__total {
    font-size: 2rem;
    font-weight: normal; }
  .activity__percent {
    color: #1d83c6; }
    .activity__percent--blue {
      color: #1d83c6; }
    .activity__percent--ocean {
      color: #23c6c7; }
    .activity__percent--green {
      color: #19b393; }
    .activity__percent--red {
      color: #ed5564; }

.graphs-collection {
  padding-left: 0.5rem; }
  .graphs-collection__total {
    font-size: 1.25rem;
    font-weight: normal; }
  .graphs-collection__progress {
    margin: 0.5rem 0; }
  .graphs-collection__progress-bar {
    background: #19b393;
    height: 0.3rem; }
  .graphs-collection__icon {
    color: #23c6c7; }

.orders-in-graphs__btn {
  border: solid 1px #ccc;
  background: #fff;
  color: #959799;
  padding: 0.2rem 0.5rem;
  border-radius: 2px;
  letter-spacing: 1px;
  cursor: pointer;
  -webkit-transition: 0.4s;
  transition: 0.4s;
  box-shadow: inset 0px 0px 4px 1px rgba(0, 0, 0, 0); }
  .orders-in-graphs__btn--active {
    box-shadow: inset 0px 0px 4px 1px rgba(0, 0, 0, 0.35); }

.orders-in-graphs__graph-container {
  display: flex;
  align-items: center; }

.orders-in-graphs__img {
  width: 77%; }

.orders-in-graphs__collection {
  width: 23%; }

.partial-content__btn {
  border: 0;
  margin: 0 0.3rem;
  background: transparent;
  color: #ccc;
  -webkit-transition: 0.4s;
  transition: 0.4s;
  outline-width: 0; }
  .partial-content__btn:hover {
    color: #8b8a8a;
    cursor: pointer; }

.partial-content__children {
  overflow: hidden; }

.partial-content__dots {
  color: #555;
  font-size: 2rem;
  line-height: 0;
  letter-spacing: 1px; }

.messages__new {
  background: #f3f6fb; }

.messages__title {
  font-size: 2rem; }

.messages__item {
  border-bottom: 1px solid #ccc;
  padding: 0.5rem 0; }

.messages .highlight {
  color: #19b393; }

.projects__item {
  display: flex;
  align-items: center;
  padding: 0.3rem 1rem; }
  .projects__item:not(:last-child) {
    border-bottom: 1px solid #ccc; }

.projects__cell {
  width: 25%;
  min-height: 2rem;
  display: flex;
  align-items: center; }
  .projects__cell--narrow {
    width: 15%; }
  .projects__cell--wider {
    width: 35%; }

.projects__cell-value {
  color: #19b393; }

.projects__cell-status--completed {
  color: #fff;
  display: inline-block;
  padding: 0.2rem 0.5rem;
  border-radius: 2px;
  letter-spacing: 1px;
  background: #19b393; }

.projects__cell-status--canceled {
  color: #fff;
  display: inline-block;
  padding: 0.2rem 0.5rem;
  border-radius: 2px;
  letter-spacing: 1px;
  background: #ed5564; }

.todo__item {
  display: flex;
  align-items: center;
  background: #f3f3f4;
  border-radius: 4px;
  margin: 0.2rem 0;
  padding: 0.62rem; }

.todo__text {
  margin-left: 0.5rem; }
  .todo__text--done {
    text-decoration: line-through; }

.search-form {
  padding: 0.5rem 0; }
  .search-form__input {
    padding: 0.3rem; }
  .search-form .incorrect {
    border: 1px solid #6b2d2d; }

.transactions__container {
  display: flex;
  justify-content: space-between; }

.transactions__item {
  display: flex;
  align-items: center;
  padding: 0 0.5rem;
  min-height: 2rem;
  display: flex;
  align-items: center;
  border-bottom: 1px solid #ccc; }

.transactions__inner {
  width: 48%; }

.transactions__cell-num {
  width: 10%; }

.transactions__cell-transaction {
  width: 40%; }

.transactions__cell-date {
  text-align: center;
  width: 25%; }

.transactions__cell-amount {
  width: 25%;
  text-align: center; }

.transactions__amount {
  color: #fff;
  display: inline-flex;
  align-items: center;
  padding: 0.2rem 0.5rem;
  border-radius: 2px;
  letter-spacing: 1px;
  background: #19b393; }

.home {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  align-items: start;
  grid-template-areas: 'income orders visits user-activity' 'orders-in-graphs orders-in-graphs orders-in-graphs orders-in-graphs' 'content-collection content-collection content-collection content-collection';
  grid-gap: 1.5rem; }
  .home .income {
    grid-area: income; }
  .home .orders {
    grid-area: orders; }
  .home .visits {
    grid-area: visits; }
  .home .user-activity {
    grid-area: user-activity; }
  .home .orders-in-graphs {
    grid-area: orders-in-graphs; }
  .home .home__content-collection {
    grid-area: content-collection; }

.home__content-collection {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  align-items: start;
  grid-template-areas: 'messages-container lists-container lists-container';
  grid-gap: 1.5rem; }

.home__messages-container {
  grid-area: messages-container; }

.home__lists-container {
  grid-area: lists-container;
  align-items: start;
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-template-areas: 'projects todo' 'transactions transactions';
  grid-gap: 1.5rem; }
  .home__lists-container .projects {
    grid-area: projects; }
  .home__lists-container .todo {
    grid-area: todo; }
  .home__lists-container .transactions {
    grid-area: transactions; }

